.navigation {
  position: relative;
  z-index: 50;
  display: flex;
  flex-direction: column;
  align-items: space-between;
  height: 100%;
  min-width: 78px;
  width: 78px;
  background-color: var(--nav-bar-color);
}

@media only screen and (max-width: 480px) {
  .navigation {
    flex-direction: row;
    overflow: hidden;
    width: 100vw;
    min-height: 70px;
    height: 70px;
  }
}

.nav-items {
    width: 100%;
    margin: auto 0;
    display: flex;
    flex-direction: column;
    -webkit-tap-highlight-color: transparent;
}

@media only screen and (min-width: 800px) and (min-height: 770px) {
    .nav-items {
        -webkit-transform: translateY(-30%);
                transform: translateY(-30%);
    }
}

@media only screen and (max-width: 480px) and (orientation: portrait) {
    .nav-items{
        width: auto;
        flex-direction: row;
        justify-content: space-between;
        margin: 0 auto;
    }
}

@media only screen and (max-width: 760px) and (orientation: landscape) {
    .nav-items {
        justify-content: space-between;
        -webkit-transform: translateY(-25%);
                transform: translateY(-25%);
    }
}

@media (max-width: 350px) {
    .nav-items {
        -webkit-transform: translateX(130%);
                transform: translateX(130%);
        transition: -webkit-transform .2s ease-out;
        transition: transform .2s ease-out;
        transition: transform .2s ease-out, -webkit-transform .2s ease-out;
        /* display: none; */
    }
}
.nav-item {
  width: 100%;
  height: 50px;
  line-height: 70px;
  text-align: center;
  justify-content: center;
  text-transform: uppercase;
  display: flex;
  margin: auto;
}
.nav-link {
  text-decoration: none;
}

.nav-link:link,
.nav-link:visited,
.nav-link:active {
  color: var(--cyan);
}

.nav-item-text {
  cursor: pointer;
  font-size: 0.8rem;
  margin: 0 3px;
  overflow: visible;
  text-align: center;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  opacity: 0;
  transition: all 0.25s ease-out;
}
.nav-item .active svg {
  color: var(--cyan);
}

.nav-item:hover .nav-item-text {
  opacity: 1;
  color: var(--cyan);
}

.nav-item:hover svg {
  opacity: 0;
}
.nav-item svg {
  transition: all 0.35s ease-out;
  color: rgb(83, 83, 83);
  opacity: 1;
}

@media only screen and (max-width: 480px) and (orientation: portrait) {
  .nav-item {
    justify-content: center;
    height: 70px;
    padding: 0 7px;
    margin: 0 auto;
  }
  .nav-item:hover .nav-item-text {
    opacity: 0;
  }
  .nav-item:hover svg {
    opacity: 1;
    color: var(--cyan);
  }
  .nav-item-text {
    display: none;
  }
}

@media only screen and (max-width: 760px) and (orientation: landscape) {
  .nav-item {
    padding: 30px 0;
    height: 35px;
  }
}

@media only screen and (orientation: landscape) and (max-height: 620px) {
  .nav-item svg {
    transition: 0s;
    transition-delay: 0s;
    visibility: hidden;
  }
}

