.web-dev-svg {
    width: 90%;
    stroke-dashoffset: 0;
    fill: rgb(226, 226, 226);
    stroke: #1d1d1d;
    stroke-width: 1;
}
.web-dev-svg.first-render {
    fill: transparent;
    stroke: rgb(226, 226, 226);
    stroke-dasharray: 430;
    stroke-dashoffset: 430;
    -webkit-animation: draw 2.5s 0s forwards ease-in, paintWebDev 2s 2.5s forwards ease;
            animation: draw 2.5s 0s forwards ease-in, paintWebDev 2s 2.5s forwards ease;
}


@-webkit-keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}


@keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}
@-webkit-keyframes paint {
    to {
        fill: #07e4c39a;
    }
}
@keyframes paint {
    to {
        fill: #07e4c39a;
    }
}
@-webkit-keyframes paintWebDev {
    to {
        fill: rgb(226, 226, 226);
        stroke: #1d1d1d;
        stroke-width: 1;
    }
}
@keyframes paintWebDev {
    to {
        fill: rgb(226, 226, 226);
        stroke: #1d1d1d;
        stroke-width: 1;
    }
}
@-webkit-keyframes strokecolor {
    to {
        stroke: rgba(17, 17, 17, 0.747);
        stroke-width: 4px;
    }
}
@keyframes strokecolor {
    to {
        stroke: rgba(17, 17, 17, 0.747);
        stroke-width: 4px;
    }
}

