}

.transition-progressbar {
    display: flex;
    width: 90%;
    margin: 0 auto;
    height: 2px;
    background-color: rgb(27, 94, 87);
}

.transition-progressbar::after {
    content: '';
    position: relative;
    width: 0%;
    background-color: var(--cyan);
    -webkit-animation: loading 2.3s forwards;
            animation: loading 2.3s forwards;

}

@-webkit-keyframes loading {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
}

@keyframes loading {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
}
