.skill-item {
    position: relative;
    display: flex;
    justify-content: center;
    text-align: center;
    width: 115px;
    height: 170px;
    margin: 10px 5px;
    border-radius: 3px;
    color: whitesmoke;
    text-shadow: 0 0 3px black;
    font-size: 1.2rem;
    cursor: pointer;
    overflow: hidden;
    transition: -webkit-transform .3s;
    transition: transform .3s ;
    transition: transform .3s, -webkit-transform .3s;
    
}

.skill-item:hover {
    -webkit-transform: scale(1.1);
