.contact {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex: 1 1;
  color: var(--text);
}

.contact-left {
  width: 49%;
  display: flex;
  flex-direction: column;
}

.contact-right {
  display: flex;
  margin: auto;
  width: 49%;
  height: 90%;
}

.contact-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  height: 100%;
}

.contact-title {
  width: 100%;
  text-align: center;
  font-size: 2em;
}

.contact-form {
  width: 100%;
}

.contact-form input[type="text"], .contact-form input[type="email"] {
  width: 100%;
  border: 0;
  background: #2b2b2b;
  height: 50px;
  font-size: 16px;
  color: #8d8d8d;
  padding: 0 20px;
  box-sizing: border-box;
}

.contact-form ul li {
  padding: 0;
  margin: 0;
  list-style: none;
  margin-bottom: 10px;
  margin-left: 2%;
  overflow: hidden;
  display: block;
  position: relative;
  clear: both;
}

.contact-form ul li.half {
  width: 49%;
  float: left;
  clear: none;
}

.contact-form ul li:nth-child(1) {
  margin-left: 0;
}

.contact-form ul li:nth-child(3) {
  margin-left: 0;
}

.contact-form ul li:nth-child(4) {
  margin-left: 0;
}

.contact-form textarea {
  width: 100%;
  border: 0;
  background: #2b2b2b;
  color: #8d8d8d;
  min-height: 150px;
  resize: vertical;
  overflow: auto;
}

.input-field {
  width: 100%;
  border: none;
  background: #2b2b2b;
  height: 50px;
  font-size: 16px;
  color: #8d8d8d;
  padding: 0 20px;
}

.submit-btn {
  color: #08fdd8;
  font-size: 13px;
  letter-spacing: 3px;
  text-decoration: none;
  padding: 8px 10px;
  border: 1px solid #08fdd8;
  background: none;
  border-radius: 4px;
  float: right;
  text-align: center;
  margin-right: 10px;
  cursor: pointer;
  text-rendering: optimizeLegibility;
  white-space: nowrap;
}

.submit-btn:hover {
  background-color: #08fdd8;
  color: #2b2b2b;
}
